___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
  margin: 24px;
}

#game {
  display: grid;
  grid-template-columns: repeat(4, 120px);
  grid-template-rows: repeat(4, 120px);
  gap: 10px;
  cursor: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) 16 16, pointer;
}

.cell {
  width: 120px;
  height: 120px;
  border: 2px solid #333;
  border-radius: 12px;
  display: grid;
  place-items: center;
  background: #f6f6f6;
}

.goblin {
  width: 80px;
  height: 80px;
  user-select: none;
  pointer-events: none;
}

.panel {
  display: flex;
  gap: 16px;
  margin-bottom: 16px;
  font-size: 18px;
  font-weight: 600;
}

.panel__item {
  padding: 8px 12px;
  border: 2px solid #333;
  border-radius: 10px;
  background: #fff;
}

.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.55);
  display: grid;
  place-items: center;
  padding: 16px;
  z-index: 9999;
}

.modal-overlay.hidden {
  display: none;
}

.modal {
  width: min(420px, 100%);
  background: #fff;
  border: 2px solid #333;
  border-radius: 14px;
  padding: 18px;
  box-shadow: 0 10px 30px rgba(0,0,0,.25);
}

.modal__title {
  margin: 0 0 10px;
  font-size: 22px;
}

.modal__text {
  margin: 0 0 16px;
  font-size: 16px;
}

.modal__actions {
  display: flex;
  justify-content: flex-end;
}

.modal__btn {
  padding: 10px 14px;
  border-radius: 10px;
  border: 2px solid #333;
  background: #f6f6f6;
  font-weight: 700;
  cursor: pointer;
